<template>
    <el-row v-loading="onLoad">
        <el-col>
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>首页</el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/Notes/' + $route.params.pagenum }">笔记列表</el-breadcrumb-item>
                <el-breadcrumb-item>笔记详情</el-breadcrumb-item>
            </el-breadcrumb>
            <vue-markdown v-highlight :source="content"></vue-markdown>
        </el-col>
    </el-row>
</template>

<script>
import VueMarkdown from 'vue-markdown'

import hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'

const highlightCode = () => {
    const preEl = document.querySelectorAll('pre')

    preEl.forEach((el) => {
        hljs.highlightBlock(el)
    })
}


export default {
    data(){
        return {
            onLoad: true,
            fileName: '',
            content: ''
        }
    },
    components: {
        VueMarkdown
    },
    created() {
        //获取到文件名称
        this.fileName = this.$route.params.id;

        fetch("../../Notes/List/" + this.fileName)
        .then(resp => resp.text())
        .then((data) => {
            this.content = data;
            this.onLoad = false;
        });
    },
    mounted () {
        highlightCode()
    },

    updated () {
        highlightCode()
    }
}
</script>

<style>
li {
    line-height: 2em;
}
code, pre {
    border-radius: 3px;
    background-color:#f7f7f7;
    color: inherit;
}

code {
    font-family: Consolas, Monaco, Andale Mono, monospace;
    margin: 0 2px;
}

pre > code {
    border: 0;
    display: inline;
    max-width: initial;
    padding: 0;
    margin: 0;
    overflow: initial;
    line-height: inherit;
    font-size: .85em;
    white-space: pre;
    background: 0 0;

}

code {
    color: #666555;
}
code{
    background:#fff;
    border:solid 1px #e1e4e5;
    /* color:#000;  for clickable code */
    font-family:Consolas,"Andale Mono WT","Andale Mono","Lucida Console","Lucida Sans Typewriter","DejaVu Sans Mono","Bitstream Vera Sans Mono","Liberation Mono","Nimbus Mono L",Monaco,"Courier New",Courier,monospace;
    font-size:75%;
    max-width:100%;
    overflow-x:auto;
    padding:0 5px;
    white-space:nowrap;
}
</style>
