<template>
    <el-row v-loading="loading">
        <el-row v-if="showNotes.length > 0" >
            <h2>全部笔记</h2>
            <note 
			v-for="note in showNotes" 
			:info="note" 
			:nowPage="nowPage"
			:key="note.title"
			></note>
        </el-row>
        <el-row style="margin-top: 2em" type="flex" justify="center">
            <div class="block">
                <el-pagination
                layout="prev, pager, next"
                :total="total"
                :pager-count="5"
                @current-change="changePage"
				:current-page="nowPage">
                </el-pagination>
            </div>
        </el-row>
    </el-row>
</template>

<script>
import Note from "../components/Note.vue";

export default {
  data() {
    return {
      everPageShowRows: 5,
      nowPage: 1,
      total: 70,
      showNotes: [],
      notes: [],
      loading: true
    };
  },
  methods: {
    changePage(pager) {
      //更换页面
      this.nowPage = pager;
      let everpage = this.everPageShowRows;
      this.showNotes = this.notes.slice(
        (pager - 1) * everpage,
        (pager - 1) * everpage + 5 > this.notes.length
          ? this.notes.length
          : (pager - 1) * everpage + 5
      );
    },
    showPagination() {
      this.total =
        this.notes.length % this.everPageShowRows == 0
          ? (this.notes.length / this.everPageShowRows) * 10
          : Math.ceil(this.notes.length / this.everPageShowRows) * 10;
	}
  },
  components: {
    note: Note
  },
  created() {
    fetch("../../Notes/list.html")
      .then(resp => resp.json())
      .then(data => {
        this.notes = data;
        this.loading = false;
        this.showPagination();
        this.changePage(this.$route.params.pagenum);
      });
  }
};
</script>

